@import url("https://fonts.googleapis.com/css2?family=Familjen+Grotesk&display=swap");

body{
  margin: 0;
  min-height: 100vh;
  background-color: #ededed;
  font-family: 'Familjen Grotesk';
  overflow-x: hidden;
}

:root{
  --black: #2C2C34;
  --grey: #a1a1a1;
  --white: #FFFFFF;
  --green: #adff2f;
  --blue: #4B40EE;
  --red: #E71D36;

  --fs-1: 1.8rem;
  --fs-2: 1.4rem;
  --fs-3: 1.2rem;
  --fs-4: 0.8rem;
  --fs-5: 0.6rem;
}
.text-black{color:var(--black);}
.text-blue{color: var(--blue);}
.text-white{color:var(--white);}
.text-grey{color:var(--grey);}
.text-red{color:var(--red);}

.bg-black{background-color:var(--black);}
.bg-blue{background-color: var(--blue);}
.bg-grey{background-color:var(--grey);}
.bg-white{background-color:var(--white);}
.bg-green{background-color:var(--green);}
.bg-red{background-color:var(--red);}


.fs-1{font-size: var(--fs-1);}
.fs-2{font-size: var(--fs-2);}
.fs-3{font-size: var(--fs-3);}
.fs-4{font-size: var(--fs-4);}
.fs-5{font-size: var(--fs-5);}

.fw-600{font-weight: 600}

.uppercase{text-transform: uppercase;}

.container {
  position: relative;
  margin-inline: auto;
  padding-bottom: 6em;
  padding: 1em;
  max-width: 80rem;
  overflow-x: hidden;
  min-height: 100vh;
}

.center{
  display: flex;
  justify-content: center;
}

.line-height-1{
  line-height: 2rem;
}


.grid{
  display: grid;
}

.flex{
  display: flex;
}

.flex-vert{
  display: flex;
  flex-direction: column;
}

.gap-2{
  gap: 2em;
}
.gap-1{
  gap: 1em;
}
.gap-0{
  gap:0.5em;
}

.space-between{
  justify-content: space-between;
}

.space-around{
  justify-content: space-around;
}

.align-baseline{
  align-items: baseline;
}
.align-center{
  align-items: center;
}
  
.text-center{
  text-align: center;
}

.text-left{
  text-align: end;
}
  
.large-only{
  display: none;
}

.align-text-vert{
  vertical-align: middle;
}

.header .get-started-button{
  border-radius: 10px;
  padding:0.3rem 0.5rem;
}

.land-text{
  margin-bottom: 2rem;
}

.land-text .get-started-button{
  border-radius: 10px;
  padding:0.5rem 0.8rem;
}

.card{
  box-shadow: 5px 10px 8px #d2d2d2;
  border-radius: 8px;
  padding: 1em;
  margin-bottom: 2em;
}
.balance{
  max-width: min-content;
}

.header{
  padding-inline: 1em;
}

.small-img{
  width: 20px;
  height: 20px;
  margin: 0;
}
.large-img{
  width: 60px;
  height: 60px;
}
.small-chart{
  width: 6rem;
  height: 4rem;
}

.coin-page-header{
  margin-top: 3rem;
  align-items:center;
  line-height: 0;

}

.coin-page-chart{
  margin-inline: auto;
}

.chart-container {
  position: relative;
  width: 100%;
  height: 40vh;
}

.coin-page{
  margin-inline: auto;
  width: 100%;
}

.mobile-footer{
  border-radius: 5px 5px 0 0;
  position: fixed;
  bottom: 0;
  padding-block: 1em;
  width: 100%;
  justify-content: space-evenly;
}

.ranges {
  margin-block: 2em;
  margin-inline: auto;
  display: flex;
  gap: 10px;
}
.range-button {
  width: 40px;
  height: 25px;
  background: white;
  border-radius: 5px;
}

.chart-ranges{
  flex-direction: column-reverse;
}



.portfolio-page{
  margin-inline: auto;
}

.portfolio-page table{
  margin-inline: auto;
  width: 25em;
}

.order{
  margin-block: 0;
  width: 100%;
  padding-block: 0.2em;
  border-radius: 5px;
  text-align: center;
}

.order{
  margin-inline: auto;
  padding-block: 0.5em;
  width: 75%;
}

.buy-button{
  display: block;
  margin-top: 3em;
  text-align: center;
  border-radius: 5px;
  width: 8.5rem;
  padding: 0.8rem;
  margin-inline:auto;
}


.order-container{
  z-index:3;
  transition: transform 250ms cubic-bezier(.5, 0, .5, 1);
  position: fixed;
  height: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  padding: 1em;
}
.overflow-wrapper{
  overflow: hidden;
}
.order-button{
  display: inline-block;
  padding: 0.5em;
  text-align: center;
  border-radius: 5px;
}
.order-open{
  transform: translateX(0%);
}
.order-closed{
  transform: translateX(102%);
}
.count-button{
  border-radius: 5px;
  width: 20%;
  vertical-align: middle;
}
.count-input{
  border: solid 3px var(--blue);
  width: 60%;
  border-radius: 5px;
}

.input-container{
  width: 100%;
  margin-inline:auto;
}

.number-grid{
  margin: 1.5em auto;
  grid-template-columns: repeat(3,1fr);
  width: 70%;
}

.number-grid > button{
  aspect-ratio: 1;
}

.table{
  align-self:flex-start ;
  text-align: left;
  margin-inline: auto;
}

.order-popup{
  text-align: center;
  z-index: 10;
  position: absolute;
  display: flex;
  top: 0;
  bottom:0;
  left: 0;
  right: 0;
  margin: auto;
  width: 15em;
  height: 15em;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: transform 250ms cubic-bezier(.5, 0, .5, 1);
}

.order-popup-open{
  visibility: visible;
  transform: scale(1);
}

.order-popup-closed{
  transform: scale(0);
}

.icon-container{
  margin-block:0;
  margin-inline: auto;
  width: 3em;
  height: 3em;
  padding: 1em;
  border-radius: 50%;
}
  
tr{
  height: 4em;
}
th{ 
  width: 0;
}

button {
  font-family: inherit;
  border: none;
  background: none;
  cursor: pointer;
  padding: 0.5em;
}
.pointer{
  cursor: pointer;
}

a{
  text-decoration: none;
  color: inherit;
}

.search{
  padding-top: 2em;
  width: 100%;
  height: 100vh;
  position: fixed;
  overflow: hidden;
  background-color: var(--black);
  z-index: 5;
  top: 0;
  left: 0;
  align-items: flex-start;
  transition: transform 250ms cubic-bezier(.5, 0, .5, 1);
}

.search-closed{
  transform: translateX(300%);
}

.search-open{
  transform: translateX(0%);
}

.search-bar{
  width: 400px;
}

.option{
  align-items: center;
}

.star-icon{
  font-size: 1.5rem;
  color: var(--blue);
}

.mobile-only{
  display: none;
}

.arrow{
  color: var(--white);
  font-size: 2rem;
}

.google-auth{
  justify-self: center;
  border-radius: 3rem;
  padding: 0.5rem 1.5rem;
}

.google-icon{
  width: 1.5rem;
}

.auth--card{
  padding: 1rem 3rem;
}


/* 

auth form

*/

.label {
  color:var(--grey);
  font-size: 1.2rem;
  position: absolute;
  pointer-events: none;
  left: 0;
  transform-origin: 0%;
  transition: transform 400ms;
}

.field:focus-within .label,
.input:not(:placeholder-shown) + .label {
  transform: scale(0.8) translateY(-1.5rem);
  opacity: 1;
}

.input {
  outline: none;
  border: none;
  overflow: hidden;
  margin: 0;
  width: 100%;
  padding: 0.25rem 0;
  background: none;
  color: var(--black);
  font-size: 1.2em;
  font-weight: bold;
  transition: border 500ms;
}

/* .input:valid{
  color: var(--green);
}

.input:invalid{
  color: var(--red);
} */

.field {
  width: 100%;
  margin: 0 auto;
  position: relative;
  margin: 2rem auto 1rem;
  transition: 500ms;
  border-bottom: 1px solid var(--grey);
}

.field::after {
  content: "";
  position: relative;
  display: block;
  height: 4px;
  width: 100%;
  background: var(--blue);
  transform: scaleX(0);
  transform-origin: 0%;
  opacity: 0;
  transition: all 500ms ease;
  top: 2px;
}


.field:focus-within {
  border-color: transparent;
}

.field:focus-within::after {
  transform: scaleX(1);
  opacity: 1;
}

.sign-in-button {
  border-radius: 3rem;
  background-color: var(--blue);
  padding: 0.5em 1em;
  color: var(--white);
}

.auth{
  position: absolute;
  bottom: 14vh;
  left: 0;
  right: 0;
  margin-inline: auto;
}

.profile-field{
  background-color: var(--black);
  color: var(--white);
  position: absolute;
  top: 10vh;
  right: 10px;
  border-radius: 10px;
  transition: scale 100ms;
  display: flex;
  flex-direction: column;
  padding: 1em;
  justify-content: center;
  transform-origin: 100%;
}
.not-display{
  scale: 0;
}

.display{
  scale: 1;
}

@media (min-width:45rem) {

  :root{
    --fs-0: 5rem;
    --fs-1: 2.4rem;
    --fs-2: 1.8rem;
    --fs-3: 1.6rem;
    --fs-4: 1.2rem;
    --fs-5: 1rem;
  }

  .fs-0{font-size: var(--fs-0);}
 
  .container{
    min-height:88vh;
  }
  
  .landing--container{
    max-height:88vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    overflow-y: hidden;
  }

  .land-text{
    width: 30rem;
    margin-bottom: 2rem;

  }

  .table{
    max-width: 80%;
  }

  .currency--table{
    width: 70rem;
    margin: 0;
  }

  .coin-page{
    padding: 0 2em 3em;
    width: 70%;
  }

  .small-img{
    width: 30px;
    height: 30px;
  }
  .large-img{
    width: 80px;
    height: 80px;
  }

  .portfolio-page{
    min-width: 50rem;
  }
  
  .portfolio-page table{
    width: 100%;
  }

  .order-container{
    box-shadow: 5px 10px 8px #d2d2d2;
    border-radius: 8px;
    padding: 1em;
    position: relative;
    width: 11em;
    height:20em;
    z-index:3;
    transition: transform 250ms cubic-bezier(.5, 0, .5, 1);
  }

  .order{
    width: 100%;
  }

  .home{
    width: fit-content;
    margin-inline: auto;
  }

  .home-grid{
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    gap: 8em;
    align-items: flex-start;
  }

  td{
    margin-right: 2em;
  }

  th.large-only{
    display: table-cell;
  }

  td.large-only{
    display: table-cell;
  }

  .large-only{
    display: initial;
  }

  .header{
    height: 12vh;
    color: var(--white);
    padding-inline: 2em;
    
  }

  .nav{
    color: var(--white);
    margin-left: 4em;
  }

  .nav > a{
    position: relative;
  }
  
  .nav > a::before{
    top: 20px;
    position: absolute;
    content: '';
    width: 100%;
    border-bottom: 2px solid var(--white);
    transform: scaleX(0);
    transition: transform 250ms cubic-bezier(.5, 0, .5, 1);
  }
  .nav > a:hover::before{
    border-bottom: 2px solid var(--white);
    transform: scaleX(1);
  }

  .watchlist-card{
    justify-content: space-between;
    width: 25em;
  }
  
  .watchlist-card > *{
    line-height: 0.5em;
  }
  
  .portfolio-card{
    width: 20em;
  }
  .portfolio-card > *{
    line-height: 0.5em;
  }

  .ranges{
    margin-inline: initial;
  }
  
  .chart-ranges{
    flex-direction: column;
  }
  
  .select{
    width: 15vw;
    z-index: 4;
  }

  .buy-button{
    margin-top: 0;
    align-self: center;
  }

  .balance{
    max-width: max-content;
  }

  .chart-container {
    max-width: 100%;
  }

  .tabs { 
    margin-top: 1.5em;
    display: flex;
    gap: 12px;
  }

  .tab {
    height: 30px;
  }
    
  .order-popup{
    z-index: 10;
    position: absolute;
    display: flex;
    top: 0;
    bottom:0;
    left: 0;
    right: 0;
    margin: auto;
    width: 30em;
    height: 20em;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: transform 250ms cubic-bezier(.5, 0, .5, 1);
  }

  .auth{
    margin-top: 3rem;
    width: 30rem;
    margin-left: 10rem;
  }

  
  
}

@media(max-width:24rem){
  .portfolio-page table{ 
    width: auto;
  }
}